.assetsBox {
    padding-top: 60px;
}

.assetsBox .ant-table.ant-table-small {
    border: none;
}

.assetsBox .ant-table.ant-table-small .ant-table-tbody > tr > td {
    border: none;
}

.assetsBox .ant-table-thead .ant-table-cell {
    background-color: transparent !important;
}

.assetsBox .ant-table-thead >tr>th {
    border: none !important;
}

.assetsBox .ant-table-thead >tr>th::before {
    height: 0px !important;
}

.assetsBox .ant-table-thead .ant-table-cell .ant-table-filter-column {
    justify-content: flex-start;
}

.assetsBox .ant-table-thead .ant-table-cell .ant-table-filter-column .ant-table-column-title {
    flex: unset;
}

.assetsBox .ant-table-thead .ant-table-cell .ant-table-column-sorters {
    justify-content: flex-start;
}

.assetsBox .ant-table-thead .ant-table-cell .ant-table-column-sorters .ant-table-column-title {
    flex: unset;
}

@media (max-width: 576px) {
    .assetsBox {
        padding-top: 18px;
    }
}
