.sellBox {
    padding: 17px 16px;
    width: 100%;
}

.sellBox .sellInfo {
    width: 100%;
}

.sellBox input:-webkit-autofill,input:-webkit-autofill:hover,input:-webkit-autofill:focus,input:-webkit-autofill:active {
    -webkit-text-fill-color: #fff;
    -webkit-background-clip: text;
    transition: background-color 5000s ease-in-out 0s;
    box-shadow: inset 0 0 20px 20px #101114;
}

