.shelfInscriptionBox {
    padding-top: 54px;
    max-width: 1200px;
}

.shelfInscriptionBox .ant-input-suffix {
    color: #848E9C;
    font-weight: 500;
}

.shelfInscriptionBox .ant-card {
    border-radius: 8px;
    border: 1px solid #2B3139;
    padding: 16px;
    height: 140px;
    width: 230px;
    flex: unset;
    position: relative;
}

.shelfInscriptionBox .ant-card .checkIconBox {
    border: 14px solid;
    border-color: #2B3139 #2B3139 transparent transparent;
    position: absolute;
    width: 0;
    height: 0;
    top: 0;
    right: 0;
    border-top-right-radius: 7px;
}

.shelfInscriptionBox .ant-card .checkIcon {
    width: 13px;
    position: absolute;
    top: 3px;
    right: 2px;
}

/* .shelfInscriptionBox .ant-card .checkIconBox:hover {
    border-color: #FDD535 #FDD535 transparent transparent;
} */

.shelfInscriptionBox .ant-card .checkIconBox.checked {
    border-color: #FDD535 #FDD535 transparent transparent;
}

.shelfInscriptionBox .ant-card.checkCard {
    border-color: #FDD535;
}

.shelfInscriptionBox .ant-card.checkCard .checkIconBox {
    border-color: #FDD535 #FDD535 transparent transparent;
}

@media (any-hover: hover) {
    .shelfInscriptionBox .ant-card:hover {
        border-color: #FDD535;
    }

    /* .shelfInscriptionBox .ant-card:hover .checkIconBox {
        border-color: #FDD535 #FDD535 transparent transparent;
    } */
}

.shelfInscriptionBox .ant-card:last-child {
    margin-right: auto;
}

.shelfInscriptionBox .ant-card-body {
    height: 100%;
    padding: 0px !important;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.shelfInscriptionBox .ant-checkbox-inner {
    width: 24px;
    height: 24px;

    &::after {
        width: 8px;
        height: 12px;
    }
}

.shelfInscriptionBox .ant-checkbox {
    font-size: 24px;
}

.shelfInscriptionFooter {
    position: fixed;
    bottom: 0;
    left: 0;
    height: 72px;
    width: 100%;
    background: #262b32;
    /* margin-top: auto; */
}

.shelfInscriptionBox .listBoxStyle {
    /* margin-top: 24px; */
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(224px, 1fr));
    gap: 10px;
    justify-content: space-between;
    padding-bottom: 82px;
}

@media (max-width: 576px) {
    .shelfInscriptionBox {
        padding-top: 18px;
        min-height: calc(var(--doc-height) - 64px);
    }
    .shelfInscriptionBox .ant-card {
        border-radius: 6px;
        padding: 16px 8px;
        height: 110px;
        width: 100%;
    }

    .shelfInscriptionFooter {
        display: none;
    }

    .shelfInscriptionMobileFooter {
        position: fixed;
        bottom: 0;
        left: 0;
        height: 72px;
        width: 100%;
        padding: 0 16px;
        /* background: #181a1f; */
    }

    .shelfInscriptionBox .ant-card .checkIconBox {
        border: 12px solid;
        border-color: #2B3139 #2B3139 transparent transparent;
        border-top-right-radius: 5px;
    }

    .shelfInscriptionBox .ant-card .checkIcon {
        width: 12px;
        position: absolute;
        top: 2px;
        right: 1px;
    }

    .shelfInscriptionBox .listBoxStyle {
        padding-bottom: 10px;
        grid-template-columns: repeat(auto-fill, minmax(47.9%, 1fr));
    }

    .shelfInscriptionBox .ant-btn-primary:disabled {
        background-color: #626262;
    }
}