.sweepBox .broomListBox {
    overflow-y: auto;
}

@media (max-width: 576px) {
    .sweepBox .gasCard {
        height: 82px;
        width: calc(33.3% - 35px);
    }
    .sweepBox .gasInfoCard {
        border-radius: 4px;
        height: 112px;
        padding: 9px 8px;
        width: 100%;
    }
    .sweepBox .broomList {
        border-radius: 4px;
        background: #101114;
        max-height: 108px;
        padding: 8px;
        width: 100%;
        overflow-y: auto;
    }
}