code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.topCard {
  position: fixed;
  top: -100px;
  left: 0;
  height: 164px;
  width: 100%;
  z-index: 2;
  background-color: #181A1F;
}
